<template>
  <section
    id="home-features"
    class="home-features"
    ref="homeFeatures"
    v-waypoint="{
      active: true,
      callback: onWaypoint,
      options: intersectionOptions
    }"
  >
    <h1>FEATURES</h1>
  </section>
</template>

<script>
export default {
  name: "Features",
  data() {
    return {
      intersectionOptions: {
        root: null, // document.querySelector("#home-features"),
        rootMargin: "0px 0px 0px 0px",
        threshold: [0.5] // [0.25, 0.75] if you want a 25% offset!
      } // https://developer.mozilla.org/en-US/docs/Web/API/Intersection_Observer_API
    };
  },
  methods: {
    onWaypoint({ going }) {
      //  onWaypoint({ going, direction })
      // going: in, out
      // direction: top, right, bottom, left
      if (going === this.$waypointMap.GOING_IN) {
        console.log("waypoint going In");
        this.$refs.homeFeatures.style.backgroundColor = "royalblue";
      }
      if (going === this.$waypointMap.GOING_OUT) {
        console.log("waypoint going Out!");
        this.$refs.homeFeatures.style.backgroundColor = "orangered";
      }

      // if (direction === this.$waypointMap.DIRECTION_TOP) {
      //   console.log("waypoint going top!");
      // }
    }
  }
};
</script>

<style></style>
