<template>
  <header ref="homeHeader" id="home-header" class="home-header">
    <video class="home-header__video" autoplay loop muted>
      <source src="~@/assets/video/header-bg-mountains.mp4" type="video/mp4" />
      Your browser is not supported!
    </video>
    <img class="home-header__logo" src="~@/assets/img/logo.svg" alt="logo" />
    <img
      data-speed="50"
      class="home-header__chuk parallax"
      src="~@/assets/img/header-chuk.png"
      alt="chuk"
    />
    <img
      data-speed="100"
      class="home-header__gamak parallax"
      src="~@/assets/img/header-gamak.png"
      alt="gamak"
    />
    <div data-speed="100" class="home-header__bag parallax">
      <div @click="isBag = !isBag" class="home-header__bag-btn"></div>
      <div
        v-if="isBag"
        @click="isBag = !isBag"
        class="home-header__bag-content"
      >
        <b>Bag</b> - Lorem ipsum dolor sit amet consectetur adipisicing elit.
        Sequi doloribus, sunt voluptas voluptate ullam saepe corrupti labore
        quaerat quos voluptatum consequuntur quod magnam animi quibusdam maxime
        fugit amet porro.
      </div>
    </div>
  </header>
</template>
<script>
export default {
  name: "Header",
  data() {
    return {
      isBag: false
    };
  },
  mounted() {
    this.$refs.homeHeader.addEventListener("mousemove", function(event) {
      this.querySelectorAll(".parallax").forEach(layer => {
        const speed = layer.getAttribute("data-speed");
        layer.style.transform = `translateX(${(event.clientX * speed) /
          9000}px)`;
      });
    });
  }
};
</script>
